.promo {
  position: relative;
  color: rgba(255, 255, 255, 1);
  font-family: $main_font;
  font-size: 14px;
  line-height: 14px;
  padding: 0 20px;

  &::before {
    position: absolute;
    content: "";
    background-image: url("../img/index-promo-background.jpg");
    background-size: contain;
    background-color: $color_basic_green;
    background-position: 50% 50%;
    background-repeat: no-repeat;
    width: 100%;
    height: 302px;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: -1;
  }
}

.promo__product-image {
  position: relative;
  z-index: 2;
  margin: 0 20px;
}

.promo__title {
  font-size: 36px;
  line-height: 36px;
  font-family: $main_font;
  font-weight: 400;
  margin: 0 auto;
  padding-top: 27px;
  padding-bottom: 25px;
  text-align: center;
}

.promo__description {
  font-size: 14px;
  line-height: 14px;
  margin: 0;
  margin-bottom: 29px;
  text-align: center;
  text-transform: uppercase;
}

.promo__button {
  max-width: 280px;
  margin: 0 auto;
  margin-bottom: 20px;
}

@media (min-width: $tablet-width) {
  .promo {
    color: #000000;
    display: flex;
    flex-direction: column;
    padding: 0 30px;

    &::before {
      display: none;
    }
  }

  .promo__title {
    max-width: 500px;
    text-align: left;
    margin: 0;
    margin-left: 119px;
    margin-bottom: 41px;
  }

  .promo__description {
    font-size: 20px;
    line-height: 20px;
    text-align: left;
    margin-left: 119px;
  }
}

@media (min-width: $desktop-width) {
  .promo {
    display: grid;
    max-width: 1440px;
    padding: 0 110px;
  }

  .promo__title {
    margin-left: 80px;
    line-height: 60px;
  }
}
